import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const store = new Vuex.Store({
	state: {
		type:3,
		hasLogin: false,
		userInfo: {},//,
	},
	mutations: {
		set_type(state, provider){
			state.type = provider
			uni.setStorage({
				key: 'type',   
				data: provider
			}) 
		},
		login(state, provider) {
			state.hasLogin = true;
			state.userInfo = provider;
			uni.setStorage({//
			    key: 'userInfo',  
			    data: provider  
			})
			uni.setStorage({//
			    key: 'is_vip',  
			    data: provider.is_vip  
			})
			uni.setStorage({//
			    key: 'token',  
			    data: provider.token 
			}) 
			uni.setStorage({//
			    key: 'hasLogin',  
			    data: true  
			}) 
			// uni.setStorage({//
			// 	key:'loginmode',
			// 	data:provider
			// })
			console.log(state.userInfo);
		},
		logout(state) {
			state.hasLogin = false;
			state.userInfo = {};
			// console.log(222)
			uni.setStorage({//
			    key: 'userInfo',  
			    data: ''  
			})
			uni.setStorage({
				 key: 'is_vip',  
				 data: ''  
			})
			uni.setStorage({//
			    key: 'token',  
			    data: ''  
			}) 
			uni.setStorage({//
				key: 'hasLogin',   
				data: false  
			}) 
			uni.redirectTo({
				url:'/pages/login/login'
			})
			// uni.setStorage({//
			// 	key:'loginmode',
			// 	data:''
			// })
		}
	},
	actions: {
	
	}
})

export default store